.noOrder {
  width: 100%;
  height: 100%;
  border: 2px solid #bbbbbb;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: #bbbbbb;
}
.entry_container {
  width: 400px;
  height: 400px;
  background: rgba(142, 198, 13, 255);
  margin: 10px;
  color: #fff;
  border-radius: 20px;
}
.container_main {
  width: calc(100vw - 64px);
  overflow: hidden;
  position: absolute;
  top: 0px;
  height: fit-content;
  max-width: 500px;
}
.navigation_btn {
  position: absolute;
  top: 50%;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
}
