body {
  margin: 0;
  box-sizing: border-box;
  overscroll-behavior: contain;
  overscroll-behavior-y: contain;
}
.App{
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  overscroll-behavior: contain;
}
.admin{
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;

  
}
.layout {
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  background-color: white;
  scroll-behavior: smooth;
  overscroll-behavior: contain;
}