.main_background {
  background-color: rgba(142, 198, 13, 255);
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0.3;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
}
.main_background .main_logo_container {
  background: #fff;
  min-width: 90vw;
  min-height: 80vh;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.3;
}
.main_background .main_logo_container img {
  width: 400px;
  height: 400px;
  object-fit: contain;
}
.main_pages {
  position: fixed;
  width:100vw;
  height:100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  z-index: 9999999;
}
.main_pages .question {
  font-size: 20px;
  font-weight: 500;
}
.main_pages .options {
  padding-top: 30px;
  font-size: 15px;
  font-weight: 500;
}
.main_pages .options button {
  font-size: 30px;
  font-weight: bolder;
  background-color: rgba(142, 198, 13, 255);
  color: #fff;
  opacity: 1;
  border: none;
  box-shadow: "0px 20px 20px #000";
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 10px;
}
.main_pages .options div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  width: 100px;
}
.main_component {
  /* border: 2px solid #000; */
  width: 90%;
  height: fit-content;
  position: relative;
  height: 600px;
}
.main_component .container {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
.question img {
  width: 100%;
  object-fit: contain;
}
.sharing_page {
  justify-content: space-between;
  flex-direction: column;
  height: 500px;
}
.sharing_page .sharing_page_image {
  position: relative;
  width: fit-content;
}
.sharing_page_image .sharing_logo_img {
  width: 350px;
  object-fit: contain;
}
#sharing_person_img {
  position: absolute;
  z-index: 99999999999999999;
  top: 16px;

  right: 35px;
  width: 163px !important; 
  height:218px !important; 
  object-fit: cover !important;
  background-color: #fff;
}
#sharing_person_img_form {
  position: absolute;
  z-index: 99999999999999999;
  top: 14px;

  right: 30px;
  width: 139.5px !important; 
  height:187px !important; 
  object-fit: cover !important;
  background-color: #fff;
}
